html{overflow-x:hidden}body{margin:0;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-attachment:fixed;background-image:linear-gradient(rgba(34,33,33,0.75), rgba(34,33,33,0.75)),url(/lowPolyRobotsAttack/static/media/background.b596faa7.gif);background-size:cover;background-repeat:no-repeat;font-family:'Press Start 2P', cursive;overflow-x:hidden;position:relative}.app{display:grid;grid-template-rows:auto auto auto;grid-template-areas:"about" "title" "level-selection"}.about{grid-area:about;justify-self:end;color:white;padding:1rem}a{color:white;text-decoration:underline}a>h5:hover{color:#FF3CC3;text-decoration:underline}.about h5{margin-right:2rem;text-decoration:underline}.about a{color:white}.title{grid-area:title;justify-self:stretch;align-self:stretch;text-align:center}.title div{margin-top:12vh}.glow{color:#FF83CF;text-align:center;-webkit-animation:glow 1s ease-in-out infinite alternate;animation:glow 1s ease-in-out infinite alternate}@-webkit-keyframes glow{from{text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}to{text-shadow:0 6px 3px #D11087,0 6px 8px #D11087,0 8px 10px #D11087,0 12px 40px #D11087}}@keyframes glow{from{text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}to{text-shadow:0 6px 3px #D11087,0 6px 8px #D11087,0 8px 10px #D11087,0 12px 40px #D11087}}.title h4{color:#FF83CF}.title h5{color:#D11087}.title h6{color:white}.lvl-selection{align-self:center;justify-self:center;height:100%;width:100%;grid-area:level-selection;background-color:rgba(0,0,0,0.6)}.lvl-selection .card-container{display:grid;grid-template-rows:auto;grid-template-columns:repeat(auto-fit, minmax(220px, 1fr));align-self:center;text-align:center;grid-gap:2.5rem;padding:2vw}.card{justify-self:center;padding:2vw;width:80%;border:2px solid #00B9F0;color:white;text-align:center;transition-timing-function:ease-out;transition:0.2s;display:grid;grid-template-rows:auto 10% auto auto;grid-template-columns:100%;grid-template-areas:"lvl" "ttl" "short-desc" "card-btn"}.card:hover{transition-timing-function:ease-in;transition:0.2s;transform:translateY(-5%);-webkit-animation:glow-card 1s ease-in-out infinite alternate;animation:glow-card 1s ease-in-out infinite alternate}@-webkit-keyframes glow-card{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #00B9F0,0 6px 8px #FF3CC3,0 8px 10px #00B9F0,0 12px 40px #00B9F0}}@keyframes glow-card{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #00B9F0,0 6px 8px #FF3CC3,0 8px 10px #00B9F0,0 12px 40px #00B9F0}}.card div{grid-area:lvl;align-self:center;justify-self:center}.card h4{grid-area:ttl;color:#00B9F0;align-self:center;justify-self:center}.card p{grid-area:short-desc;text-align:center;width:100%;align-self:start;justify-self:start}.card button{align-self:start;justify-self:start;grid-area:card-btn;margin-top:1vw;width:100%;height:80%;font-family:'Press Start 2P', cursive;background-color:#00B9F0;border:none}.play{border:2px solid #FF83CF}.play:hover{-webkit-animation:glow-card-pink 1s ease-in-out infinite alternate;animation:glow-card-pink 1s ease-in-out infinite alternate}@-webkit-keyframes glow-card-pink{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #FF83CF,0 6px 8px #FF83CF,0 8px 10px #FF83CF,0 12px 40px #FF83CF}}@keyframes glow-card-pink{from{box-shadow:0 6px 3px #303035,0 6px 8px #303035,0 8px 10px #303035,0 12px 40px #303035}to{box-shadow:0 6px 3px #FF83CF,0 6px 8px #FF83CF,0 8px 10px #FF83CF,0 12px 40px #FF83CF}}.play h1{color:#FF3CC3}.card.play h4{color:rgba(0,0,0,0)}.play button{background-color:#FF3CC3}

.authors-section{display:grid;grid-template-columns:repeat(auto-fit, minmax(320px, 1fr))}.information-col{justify-self:stretch;align-self:stretch;height:100%;width:100%}.information-col h1{margin:4vw;color:#FF83CF;text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}.author-desc{display:grid;grid-template-rows:10% auto 35%;grid-template-columns:40% 60%;grid-template-areas:"img author" "img task" "img link";margin-top:2vw;margin-bottom:2vw;color:white}.img-container{grid-area:img;justify-content:center;align-self:center}.img-container img{height:90%;width:100%}.author-name{grid-area:author;align-self:stretch;justify-self:start}.author-name.Sergio{text-shadow:0 3px 3px #FF3CC3,0 3px 3px #FF3CC3,0 6px 10px #FF3CC3,0 8px 40px #FF3CC3}.author-name.Andrea{text-shadow:0 3px 3px #00FF27,0 3px 3px #00FF27,0 6px 10px #00FF27,0 8px 40px #00FF27}.author-task{grid-area:task;align-self:stretch;justify-self:start;font-family:'VT323', monospace;width:80%}.author-task p{font-size:2rem}.author-links{grid-area:link;align-self:stretch;justify-self:start;font-family:'VT323', monospace}.author-links h5{display:inline;position:relative;bottom:1rem}.author-links span svg{height:3rem}.author-links span svg:last-child{margin-left:1rem}.Sergio-label{color:#FF3CC3}.Sergio-label a{color:#FF3CC3}.Andrea-label{color:#00FF27}.Andrea-label a{color:#00FF27}.Andrea svg path{fill:#00FF27}.ghost-col{justify-self:stretch;align-self:stretch;color:white;padding:2vw;text-align:center}.ghost-col .ghost-desc,.ghost-col h4{text-shadow:0 3px 3px #FF3CC3,0 3px 3px #FF3CC3,0 6px 8px #FF3CC3,0 6px 10px #FF3CC3}.ghost-col div{text-align:center}.ghost-col svg{width:40%;height:40%;margin:1rem;-webkit-animation:glow-ghost 1s ease-in-out infinite alternate;animation:glow-ghost 1s ease-in-out infinite alternate}@-webkit-keyframes glow-ghost{from{-webkit-filter:drop-shadow(0 6px w0px #FF3CC3);filter:drop-shadow(0 6px w0px #FF3CC3)}to{-webkit-filter:drop-shadow(0 6px 20px #D11087);filter:drop-shadow(0 6px 20px #D11087)}}@keyframes glow-ghost{from{-webkit-filter:drop-shadow(0 6px w0px #FF3CC3);filter:drop-shadow(0 6px w0px #FF3CC3)}to{-webkit-filter:drop-shadow(0 6px 20px #D11087);filter:drop-shadow(0 6px 20px #D11087)}}.ghost-col h4{margin-top:8vw;margin-bottom:2vw}#pixeled_git path:hover{fill:#b8b3b3;stroke:white;stroke-width:2px}#pixeled_lnkdn path:hover{fill:#07f;stroke:white;stroke-width:2px}

.chapter{padding:4vw;background-color:#303035}.chapter h2{color:#FF3CC3}.chapter h3{color:#FF83CF;text-decoration:underline}.chapter h5{color:white;text-decoration:underline}.chapter p{color:white;width:90vw;font-family:'VT323', monospace}.chapter-title,.chapter-content h1{color:#FF83CF;-webkit-animation:glow 1s ease-in-out infinite alternate;animation:glow 1s ease-in-out infinite alternate}@-webkit-keyframes glow{from{text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}to{text-shadow:0 6px 3px #D11087,0 6px 8px #D11087,0 8px 10px #D11087,0 12px 40px #D11087}}@keyframes glow{from{text-shadow:0 6px 3px #FF3CC3,0 6px 8px #FF3CC3,0 8px 10px #FF3CC3,0 12px 40px #FF3CC3}to{text-shadow:0 6px 3px #D11087,0 6px 8px #D11087,0 8px 10px #D11087,0 12px 40px #D11087}}

